
.login-logo{
	background:#3190E8 ;
    position: fixed;;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 1.95rem;
}
a{
	text-decoration: none;
}
.page-back{
	display: inline-block;
    top: 0px;
    left: 10px;
    position: absolute;
    width: 40px;
    height: 40px;
    text-align: center;
}
.icon-style{
     background: #3190E8;
     color:white;
     top: -2px;
}
.icon-style  .mint-cell-wrapper{
    border-bottom:none;
    background-image:  none;
}
.span-text{
    font-size:0.8rem;
    color:#fff;
    text-align:center;
    font-weight:bold;
    position: absolute;
    top: 9px;
    left: 38%;
}
.mint-cell-wrapper{
    border-bottom:1px solid #eaeaea;
    background-image:  none;
}



