
a.router-link-active {
 color: #3190e8;
}
.icon {
   width: 1em; 
   height: 1em;
   vertical-align: -0.15em;
   fill: #919191;
   overflow: hidden;
}
a{
  text-decoration: none;
}
.order{
	width:100%;
	height:600px;
	background:yellow;
}
.footer{
    width: 100%;
    height: 2.4rem;
    background: #fff;
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    z-index: 100px;
}
.footer-text{
    font-size: .4rem;
}
.child-link{
    display: inline-block;
    width: 23%;
    height: 100%;
    margin: 0;
    padding: 0.02rem 0rem;
}
.store-span{
    font-size: .7rem;
    opacity: 0.5;
}



