
.detail1{
		width: 100%;
		height: 1000px;
		 
		margin-top: 151px;
}
.menu{
		width: 20%;
		height: 610px;
		background: white;
		position: fixed;
}
.foods{
	    width: 77%;
		height: 610px;
		 
		position: absolute;
		left: 23%;
		position: absolute;
}
.menu ul li{
		list-style: none;
		font-size:.7rem;
		line-height: 80px;
        /*text-align: center;*/
        border-bottom: 1px solid  #ddd;
        width: 100%;
        height: 80px;
}
.menu ul li:first-child{
		 border-left: 4px solid red;
}
.menu ul li:hover{
		 border-left: 4px solid red;
}

	 
	/*a.router-link-active{
				color:red;
				border-bottom: 4px solid #3190e8;
				 
			}*/
ul{
		margin: 0px;
		padding: 0px;
		list-style: none;
}
.foods-p{
		font-size: 0.5rem;
		line-height: 50px;
		text-align: left;
}
.foods-span1{
		font-size: .9rem;
}
.foods-p .foods-span2{
		 opacity: .7;
}
.foods-li{
       width:100%;
       height: 140px;
        
       border-bottom: 2px solid #ddd;
}
.foods-li-left{
		width:30%;
		height:100px;
		 
		float: left;
}
.foods-li-left img{
		width: 100%;
		height: 80%;
}
.foods-li-rigth{
       width:70%;
       height: 100px;
       
       float: left;
       text-align: left;
}
.left-p1{
		font-size: 0.8rem;
		margin: 0px;
		line-height: 1.2rem;
}
.left-p2{
		font-size: 0.6rem;
		opacity: 0.5;
		line-height: 1.2rem;
}
.left-p3{
		font-size:  0.4rem;
		line-height: 1.2rem;
}
.left-p4{
		font-size: 0.8rem;
		color: orange;
		line-height: 1.2rem;
		margin-left: -3rem;
}
p{
		padding: 0px;
		margin: 0px;
}



