
a.router-link-active{
      color: #3190e8;
}
.icon {
   width: 1em; 
   height: 1em;
   vertical-align: -0.15em;
   fill: #919191;
   overflow: hidden;
}
a{
  text-decoration: none;
}
ul{
    list-style: none;
    margin:0px;
    padding:0px;
}
p{
  padding: 0px;
  margin: 0px;
}
.touxiang{
  fill: #333;
}
a{
  text-decoration: none;
  color: black;
}
.main-index{
  position:relative;
}
.xm-index{
    background:#3190E8;
    position: fixed;;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 1.95rem;
}
.head-logo{
    left: 0.4rem;
    font-size: 0.7rem;
    color: #fff;
    width: 2.3rem;
    height: 0.7rem;
    display: block;
    position: absolute;
    top: 33%;
}
.login-span{
   right: 0.55rem;
   font-size: 0.65rem;
   color: #fff;
   top:33%;
   position:absolute;
}
.swipe-slide{
    /*position:absolute;*/
    width: 100%;
    height: 9rem;
    left: -2%;
    overflow: hidden;
    border-bottom:1px solid #e0e0e0;
}
.mint-swipe{
    top: -6%;
}
.a-block{
    display:block;
    width:25%;
    height:86px;
    padding :0.3rem 0rem;
    
    float:left;
}
.a-block img{
    background-size:cover;
    display:block;
    width: 1.8rem;
    height: 1.8rem;
    margin-bottom: 0.3rem;
    margin: 0 auto;
}
.a-block span{
    display:block;
    text-align: center;
    font-size: 0.55rem;
    color: #666;
    margin-top: 0.3rem;
}
.mint-swipe-indicator.is-active{
    background:#3190E8;
    opacity:1;
}
.mint-swipe-indicator {
       
    background: black;
    opacity: .2;
}
.list-store{
    width: 100%;
    height: 1000px;
    text-align: left;
    margin-top: 1rem;
    border-top: 1px solid rgb(224, 224, 224);
}
.store-header{
    width: 100%;
    height: auto;
}
.store-shop{
    width: 100%;
    height: 100%;
    
    margin-top: .5rem;
}
.section-img{
    float: left;
    width: 20%;
    height:115px;
    display: block;
    margin:0px;
    padding:0px;
    margin-bottom: 18px;
    border-bottom: 1px solid #e0e0e0;
}
p{
    line-height: 1.4rem;
}
.section-text{
    float: left;
    width:80%;
    height: 115px;
    margin-bottom: 18px;
    border-bottom: 1px solid #e0e0e0;
}
.pinpai{
    font-size: 0.6rem;
    background: yellow;
}
.yanshi{
    font-size: .84rem;
    padding: 0 0.5rem;
}
.section-img img{
     
    width: 100%;
    height: 100%;
    display: block;
    margin-right: .4rem;
}
.bao{
    float: right;
}
.text-style{
    font-size:.5rem;
}
.text-color{
    color: #ff9a0d;
}
.svg-icon{
    font-size:0.4rem;
}
.fengniao{
    background: #3190e8;
    color: white;
    /*margin-left: 2.3rem;*/
    margin-left: 2.7rem;
}
.intime{
    border: 1px solid  #3190e8;
    border-radius: 0.5;
    margin-left: -4px;
}
.kilo{
    /*margin-left: 2.4rem;*/
    margin-left: 4.4rem;
}
ul{
    list-style: none;
    margin:0px;
    padding:0px;
}
.footer{
    width: 100%;
    height: 2.4rem;
    background: #fff;
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    z-index: 100px;
}
.footer-text{
    font-size: .4rem;
}
.child-link{
    display: inline-block;
    width: 23%;
    height: 100%;
    margin: 0;
    padding: 0.02rem 0rem;
}
.store-span{
    font-size: .7rem;
    opacity: 0.5;
}



