
.disount-p1{
	text-align: left;
	color: orange;
}
a.router-link-active{
	color: #3190e8;
	border-bottom: 4px solid #3190e8;
}
.disount-p{
	text-align: left;
}
a{
	text-decoration: none;
}
h4{
	margin:0px;
	padding:0px;
	text-align:left;
	margin-left: 0.5rem; 
	margin-top: 10px;
	color: white;
}
.p1{
	font-size:0.4rem;
    margin:0px;
	padding:0px;
	text-align:left;
	margin-left: 0.5rem; 
	 
	color: white;
	margin-top: 0.4rem;
}
.icon {
   width: 1em; height: 1em;
   vertical-align: -0.15em;
   fill: currentColor;
   overflow: hidden;
}
.icon-size1{
	position: absolute;;
	top:0px;
}
.icon-size2{
	position: absolute;
	top: 3rem;
	right: 10px;
	color: red;
}
.icon-size3{
	position: absolute;
	 
	right: 10px;
	top: 0.1rem;
     
    font-size: .2rem;
}
#detail{
		width: 100%;
		height: auto;
		
		margin-top: -60px;
		position: relative;
}
.detail-header{
		width: 100%;
		height:128px;
		background: #BEB7C6;
	    position: fixed;
	    z-index: 100;
}
.detail-fixed{
	position: fixed;
	top: 128px;
	z-index: 100;
	background: white;
	width: 100%;
	height: 50px;
	line-height: 50px;
}
.detail-fixed a{
   padding: 0 0px;
  color: black;
  margin: 0 75px;
  font-size: .7rem;
}
.detail-img{
		width:25%;
		height: 100px;
		float: left;
}
.detail-img img {
        
		width:100%;
		height: 100%;
}
.a-style{
		 
		width:100%;
		height: 100px;
}
.detail-activity{
		width: 75%;
		height: 100px;
		float: left;
}
.jian{
	 	width: 100%;
	 	height: 28px;
	 	 
	 	position: absolute;
	 	top: 100px;
}
.span1,.span2{
	 	font-size: 0.4rem;
	 	color: white;
}
.span1{
	  	float: left;
}
.span2{
	  	float: right;
	  	margin-right: 1.5rem;
}
.span11{
	  	background: red;
	  	margin-left: 0.4rem;
}
.mint-msgbox{
	  	background-color: #262626;
	  	width: 100%;
	  	height: 100%;
}
.mint-msgbox-header{
       width: 100%;
       height: 100px;
}
.mint-msgbox-title{
	  	color: white;
}
.mint-msgbox-content{
       width: 100%;
       height: 400px;
}
.mint-msgbox-btn{
	   background-color: #262626;
       width: 100%;
       height: 100px;
}



